// Font weight help class

.fw-medium {
  font-weight: $font-weight-medium;
}

.fw-semibold {
  font-weight: $font-weight-semibold;
}

.bg-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  opacity: .7;
  background-color: $black;
}

.bg-overlay-gradient{
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  opacity: .85;
  background: $header-dark-bg;
}

// Social

.social-list-item {
  height: 2.4rem;
  width: 2.4rem;
  line-height: calc(2.4rem - 2px);
  display: block;
  border: 1px solid $card-bg;
  border-radius: 50%;
  color: $text-muted;
  text-align: center;
  transition: all 0.4s;
  &:hover{
    color: $dark;
  }
}


.w-xs {
  min-width: 80px;
}
.w-sm {
  min-width: 95px;
}
.w-md {
  min-width: 110px;
}
.w-lg {
  min-width: 140px;
}

.w-xl {
  min-width: 160px;
}


// alert

.alert-dismissible {
  .btn-close{
    font-size: 10px;
    padding: $alert-padding-y * 1.4 $alert-padding-x;
  }
}

// border-color
.border-dark {
  border-color: var(--#{$variable-prefix}dark)!important;
}
.border-light {
  border-color: var(--#{$variable-prefix}light)!important;
}


[data-layout-mode="dark"]{
  .text-body{
    color: var(--#{$variable-prefix}gray-400) !important;
  }
  .btn-close{
      filter: invert(1) grayscale(100%) brightness(200%);
  }

  .btn-close-white{
    filter: invert(0) grayscale(100%) brightness(200%);
  }
}