  --bs-sidebar-dark-bg: #24243c;
  --bs-sidebar-dark-menu-item-color: #9292a9;
  --bs-sidebar-dark-menu-sub-item-color: #9292a9;
  --bs-sidebar-dark-menu-item-icon-color: #9292a9;
