* {
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 72, 118, 0.1) transparent;
}

*::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

*::-webkit-scrollbar-track {
  border-radius: 8px;
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: rgba(0, 72, 118, 0.1);
}

#page-topbar {
  position: sticky;
  top: 0;
  right: 0;
  z-index: 1002;
  background-color: var(--bs-header-bg);
  box-shadow: 0 2px 3px rgba(52, 58, 64, 0.06);
}

.navbar-header {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  height: 85px;
  padding: 0 calc(22px / 2) 0 0;
}
.navbar-header .dropdown .header-item.show {
  color: #004876;
}

.verti-dash-content {
  background: var(--bs-header-dark-bg);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding: 0 calc(22px / 2) 0 calc(22px / 2);
}

.navigation-text {
  font-size: 16px;
}

body[data-sidebar-size=sm] .navbar-navigation-box .navigation-text {
  display: none;
}

.header-navbar-brand-box {
  padding: 0 1.5rem;
  z-index: 1;
  width: 255px;
}

.navbar-navigation-box {
  width: 255px;
  color: #fff;
}
.navbar-navigation-box .nagivation-item {
  padding: 0.65rem 0.8rem;
  margin: 0 10px;
}
.navbar-navigation-box .menu-btn-wrapper {
  width: 24px;
  height: 24px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.1);
}
.navbar-navigation-box .menu-btn-wrapper .vertical-menu-btn i {
  font-size: 10px;
}

.logo {
  line-height: 85px;
}
.logo .logo-sm {
  display: none;
}

.logo-dark {
  display: block;
}

.logo-light {
  display: none;
}

.logo-txt {
  font-weight: 500;
  font-size: 20px;
  vertical-align: middle;
  margin-left: 5px;
  line-height: 1;
  color: #fff !important;
}

/* Search */
.app-search {
  width: 512px;
  padding: calc(47px / 2) 0;
}
.app-search .form-control {
  height: 40px;
  padding-left: 40px;
  padding-right: 20px;
  border-color: #eff0f2;
  background-color: var(--bs-topbar-search-bg);
  box-shadow: none;
}
.app-search span {
  position: absolute;
  z-index: 10;
  font-size: 16px;
  line-height: 38px;
  left: 13px;
  top: 0;
  color: #959396;
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
  color: #1a1823;
}

@media (max-width: 991.98px) {
  .app-search {
    width: auto;
  }

  .navbar-navigation-box {
    width: auto;
  }

  #page-topbar {
    margin-left: 0 !important;
  }
  #page-topbar .navbar-header .header-navbar-brand-box {
    display: inline-block;
    position: relative;
    width: auto;
  }
}
@media (max-width: 991.98px) and (max-width: 413px) {
  #page-topbar .navbar-header .header-navbar-brand-box .logo-txt {
    display: none;
  }
}
@media (max-width: 991.98px) {
  .logo span.logo-lg {
    display: none;
  }
  .logo span.logo-sm {
    display: inline-block;
  }
}
.page-content {
  padding: calc(85px - 33px) calc(22px / 2) 60px calc(22px / 2);
}

.header-item {
  height: 85px;
  box-shadow: none !important;
  color: var(--bs-header-item-color);
  border: 0;
  border-radius: 0px;
}
.header-item:hover {
  color: var(--bs-header-item-color);
}

.header-profile-user {
  height: 46px;
  width: 46px;
}

.user .user-item-desc {
  line-height: 1.2;
}
.user .user-name {
  font-size: 14.4px;
  display: block;
  color: #1a1823;
}
.user .user-sub-title {
  color: var(--bs-text-muted);
  font-size: 11px;
  font-weight: 600;
}

.noti-icon {
  position: relative;
}
.noti-icon > * {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
}
.noti-icon i {
  font-size: 20px;
  color: var(--bs-header-item-color);
}
.noti-icon .noti-dot {
  position: absolute;
  top: 23px;
  right: 15px;
  height: 8px;
  width: 8px;
  color: #fff;
  line-height: 13px;
  font-weight: 500;
  font-size: 10px;
}

.notification-item .d-flex {
  padding: 0.75rem 1rem;
}
.notification-item .d-flex:hover {
  background-color: var(--bs-dropdown-link-hover-bg);
}

.dropdown-icon-item {
  display: block;
  border-radius: 3px;
  line-height: 34px;
  text-align: center;
  padding: 15px 0 9px;
  display: block;
  border: 1px solid transparent;
  color: #959396;
}
.dropdown-icon-item img {
  height: 24px;
}
.dropdown-icon-item span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.dropdown-icon-item:hover {
  border-color: #f4f5f6;
}

.fullscreen-enable [data-toggle=fullscreen] .uil-minus-path::before {
  content: "\eb8d";
}

body[data-topbar=dark] #page-topbar {
  background: var(--bs-header-dark-bg);
}
body[data-topbar=dark] .navbar-header .dropdown.show .header-item {
  background-color: rgba(255, 255, 255, 0.05);
}
body[data-topbar=dark] .user-name {
  color: var(--bs-header-dark-item-color);
}
body[data-topbar=dark] .user-sub-title {
  color: rgba(255, 255, 255, 0.5);
}
body[data-topbar=dark] .noti-icon i {
  color: var(--bs-header-dark-item-color);
}
body[data-topbar=dark] .app-search .form-control {
  background-color: #F8FAFB;
  border-color: transparent;
  color: #959396;
}
body[data-topbar=dark] .app-search span,
body[data-topbar=dark] .app-search input.form-control::-webkit-input-placeholder {
  color: #959396;
}

body[data-sidebar=dark] .navbar-navigation-box {
  color: var(--bs-sidebar-dark-menu-item-color);
  background: var(--bs-sidebar-dark-bg);
}
body[data-sidebar=dark] .navbar-navigation-box .logo-dark {
  display: none;
}
body[data-sidebar=dark] .navbar-navigation-box .logo-light {
  display: block;
}

@media (max-width: 600px) {
  .navbar-header .dropdown {
    position: static;
  }
  .navbar-header .dropdown .dropdown-menu {
    left: 10px !important;
    right: 10px !important;
  }
}
@media (max-width: 380px) {
  .navbar-navigation-box {
    display: none;
  }
}
@media (max-width: 374.99px) {
  .navbar-navigation-box {
    padding: 0 1rem;
  }

  .language-switch {
    display: none !important;
  }
}
.page-title-box {
  padding: calc(22px + 12px) 0px 22px 0px;
}
.page-title-box .breadcrumb {
  background-color: transparent;
  padding: 0;
}
.page-title-box .breadcrumb .breadcrumb-item a {
  color: rgba(255, 255, 255, 0.5);
}
.page-title-box .breadcrumb .breadcrumb-item.active {
  color: #fff;
}
.page-title-box h4 {
  font-size: 18px !important;
  color: #fff;
}
@media (max-width: 575.98px) {
  .page-title-box .page-title-right {
    display: none;
  }
}

.footer {
  bottom: 0;
  padding: 20px calc(22px / 2);
  position: absolute;
  right: 0;
  color: var(--bs-footer-color);
  left: 250px;
  background-color: var(--bs-footer-bg);
  height: 60px;
  box-shadow: 0 0px 4px rgba(15, 34, 58, 0.12);
}

@media (max-width: 992px) {
  .footer {
    left: 0;
  }
}
body[data-sidebar-size=sm] .footer {
  left: 70px;
}

body[data-layout=horizontal] .footer {
  left: 0 !important;
}

.right-bar {
  background-color: var(--bs-card-bg);
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
  display: block;
  position: fixed;
  transition: all 200ms ease-out;
  width: 280px;
  z-index: 9999;
  float: right !important;
  right: -290px;
  top: 0;
  bottom: 0;
}
.right-bar .right-bar-toggle-close {
  background-color: #444c54;
  height: 24px;
  width: 24px;
  line-height: 24px;
  color: #f4f5f6;
  text-align: center;
  border-radius: 50%;
}
.right-bar .right-bar-toggle-close:hover {
  background-color: #4b545c;
}

.rightbar-overlay {
  background-color: rgba(52, 58, 64, 0.55);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
  z-index: 9998;
  transition: all 0.2s ease-out;
}

.right-bar-enabled .right-bar {
  right: 0;
}
.right-bar-enabled .rightbar-overlay {
  display: block;
}

@media (max-width: 575.98px) {
  .right-bar {
    overflow: auto;
  }
  .right-bar .slimscroll-menu {
    height: auto !important;
  }
}
.metismenu {
  margin: 0;
}
.metismenu li {
  display: block;
  width: 100%;
}
.metismenu .mm-collapse {
  display: none;
}
.metismenu .mm-collapse:not(.mm-show) {
  display: none;
}
.metismenu .mm-collapse.mm-show {
  display: block;
}
.metismenu .mm-collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition-timing-function: ease;
  transition-duration: 0.35s;
  transition-property: height, visibility;
}

.vertical-menu {
  width: 255px;
  z-index: 1005;
  background: var(--bs-sidebar-bg);
  bottom: 0;
  margin-top: 85px;
  position: fixed;
  top: 0;
  box-shadow: 0 2px 3px rgba(52, 58, 64, 0.06);
}

.main-content {
  margin-left: 255px;
  overflow: hidden;
}
.main-content .content {
  padding: 0 15px 10px 15px;
  margin-top: 85px;
}

.sidebar-menu-scroll {
  height: calc(100% - 85px);
}

#sidebar-menu {
  padding: 0px 0 30px 0;
}
#sidebar-menu .mm-active > .has-arrow:after {
  transform: rotate(-180deg);
}
#sidebar-menu .has-arrow:after {
  content: "\f0140";
  font-family: "Material Design Icons";
  display: block;
  transition: transform 0.2s;
  font-size: 1rem;
  position: absolute;
  right: 15px;
}
#sidebar-menu .has-arrow + ul .has-arrow:after {
  right: 25px;
  top: 7px;
}
#sidebar-menu .metismenu .menu-title::after {
  display: none;
}
#sidebar-menu .metismenu .menu-item {
  margin-left: 3px;
}
#sidebar-menu ul#side-menu > li > a {
  height: 60px;
}
#sidebar-menu ul.sub-menu > li > a {
  color: rgba(255, 255, 255, 0.6);
}
#sidebar-menu ul.sub-menu > li > a:hover, #sidebar-menu ul.sub-menu > li > a.active {
  color: rgba(255, 255, 255, 0.1);
}
#sidebar-menu ul li a {
  display: flex;
  padding: 0.65rem 1rem;
  color: var(--bs-sidebar-menu-item-color);
  position: relative;
  font-weight: 400;
  transition: all 0.4s;
  margin: 0 10px;
  border-radius: 3px;
  font-size: 14.4px;
  align-items: center;
}
#sidebar-menu ul li a .nav-icon {
  display: inline-block;
  min-width: 1.75rem;
  vertical-align: middle;
  color: var(--bs-sidebar-menu-item-icon-color);
  transition: all 0.3s;
  top: -1px;
  font-size: 24px;
}
#sidebar-menu ul li a:hover {
  color: var(--bs-sidebar-menu-item-hover-color);
}
#sidebar-menu ul li a:hover .nav-icon {
  color: var(--bs-sidebar-menu-item-hover-color);
}
#sidebar-menu ul li .badge {
  margin-left: auto;
}
#sidebar-menu ul li ul.sub-menu {
  padding: 0;
}
#sidebar-menu ul li ul.sub-menu li a {
  padding: 0.5rem 1.5rem 0.5rem 3.5rem;
  font-weight: 400;
  color: var(--bs-sidebar-menu-sub-item-color);
  margin: 0;
  background-color: transparent;
  font-size: 14px;
  font-family: "Archivo", sans-serif;
  margin-right: 10px;
}
#sidebar-menu ul li ul.sub-menu li a:hover {
  color: var(--bs-sidebar-menu-item-hover-color);
